@import "./colors.scss";
// @import "./button_normalize.css";
@import "./normalize.css";
@import "./onoffswitch.scss";
@import "./concentrate.scss";

/* Layout
-----------------------------------------------------------------------------*/

body {
  margin: 8px;
  color: $c_mainfg;
  background: $c_mainbg;
  display: flex;
  justify-content: center;
  /* don't select text */
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
}

button {
  font-weight: bold;
}

#wrapper {
  width: 1024px;
  max-width: 1024px;
  height: 700px;
  display: grid;
  grid-template-columns: 1024px;
  grid-template-areas:
    "header "
    "tabs-container"
    "footer ";
}

.react-tabs-container {
  grid-area: tabs-container;
  width: 1024px;
  max-width: 1024px;
  // height: 600px;
  display: grid;
  grid-template-columns: 100%;
  grid-template-areas:
    "tabs"
    "panel-main";
}


.main-tab-panel {
  grid-area: "panel-main";
  // max-height: 600px;
  max-width: 1024px;
  display: grid;
  grid-template-columns: 245px 559px 220px;
  grid-template-areas: "lsidebar content rsidebar";
}

.probe-tab-panel {
  background: #000;
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 804px 220px;
  grid-template-areas: "content rsidebar";
}

.prestige-tab-panel {
  background: #000;
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 804px 220px;
  grid-template-areas: "content rsidebar";
}

.settings-tab-panel {
  background: #000;
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 804px 220px;
  grid-template-areas: "content rsidebar";
}

/* Misc
-----------------------------------------------------------------------------*/

:focus {
  outline: none;
}
::-moz-focus-inner {
  border: 0;
}

.moneySymbol {
  color: $c_moneysymbol;
}

.knowledgeSymbol {
  color: $c_knowledgesymbol;
}

.prestigeSymbol {
  color: $c_prestigesymbol;
}

.concentrateSymbol {
  font-size: 20px;
  color: white;
}

.multiplySymbol {
  // font-size: .5em;
}

/* Header
-----------------------------------------------------------------------------*/

#header {
  height: 60px;
  grid-area: header;
  background-color: $c_headerbg;
  padding: 5px;
}

.header-wrapper {
  height: 60px;
  display: grid;
  grid-template-columns: 39% 22% 39%;
}

.income {
  font-size: 1.2em;
  /* width: 300px; */
  display: grid;
  grid-template-columns: 30% 30% 40%;
  grid-template-rows: 30px 30px;
  // float: left;
}
.income-header {
  grid-column: 1;
  grid-row: 1;
  padding: 0 10px;
  margin: auto 0 auto auto;
}
.income-money {
  grid-column: 2;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left;
}
.income-earning {
  grid-column: 3;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left;
}
.learning-header {
  grid-column: 1;
  grid-row: 2;
  padding: 0 10px;
  margin: auto 0 auto auto;
}
.learning-knowledge {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left;
}
.learning-learning {
  grid-column: 3;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left;
}

.title-wrapper {
  height: 60px;
}

.prestige {
  font-size: 1.2em;
  display: grid;
  grid-template-columns: 110px 110px 150px;
  grid-template-rows: 30px 30px;
  // float: right;
}
.prestige-header {
  grid-column: 1;
  padding: 0 10px;
  margin: auto 0 auto auto;
}
.probe-header {
  grid-column: 1;
  grid-row: 2;
  padding: 0 10px;
  margin: auto 0 auto auto;
}
.probe-distance {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left;
}
.probe-rate {
  grid-column: 3;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left;
}
.prestige-units {
  grid-column: 2;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left;
}
.prestige-earning {
  grid-column: 3;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left;
}
.prestige-boost {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left;
}

/* tabs
-----------------------------------------------------------------------------*/
#tabs {
  height: 30px;
  grid-area: tabs;
  background-color: $c_tabsbg;
  padding: 0;
  // border-bottom: 2px solid $c_tabon;
}

.tab-list {
  height: 26px;
  margin: 0;
  padding: 0 15px;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid $c_tabon;
}

.tab-list-item {
  display: inline-block;
  // width: 10em;
  width: 100px;
  text-align: center;
  margin: 0 15px;
  font-weight: bold;
  background: $c_taboff;
  border: 1px solid $c_neonblue;
  border-bottom: none;
  bottom: -1px;
  padding: 5px;
  cursor: pointer;
  color: $c_maindimfg;
}
.tab-list-item.spacer-tab {
  flex-grow: 1;
  opacity: 0;
  pointer-events: none;
}

.react-tabs__tab--selected {
  background: $c_tabon;
  color: $c_mainfg;
}

.react-tabs__tab-panel {
  display: none;
}
.react-tabs__tab-panel--selected {
  display: grid;
}



/* footer
-----------------------------------------------------------------------------*/

#footer {
  height: 41px;
  grid-area: footer;
  background-color: $c_footerbg;
  padding: 5px;
  // border: 1px solid $c_mainfg;
  border-top: 1px solid $c_neonblue;
  opacity: 0.6;
  transition: opacity 1s ease 0.3s;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-auto-rows: 50% 50%;
  // grid-template-areas:
  //   "line1"
  //   "line2";
}
#footer:hover {
  opacity: 1;
  transition: opacity 0.5s ease 0.1s;
}
#fps {
  grid-column: 1;
  grid-row: 2;
}
#savegame {
  font-weight: bold;
  opacity: 1;
}
#savegame.game-saved {
  text-align: center;
  margin: auto;
  grid-column: 8;
  grid-row: 1;
  -webkit-transition: opacity 5s ease-in;
  -moz-transition: opacity 5s ease-in;
  -ms-transition: opacity 5s ease-in;
  -o-transition: opacity 5s ease-in;
  transition: opacity 5s ease-in;
  opacity: 0;
}

/* left sidebar
-----------------------------------------------------------------------------*/

.left-sidebar {
  background-color: $c_lsidebar;
  grid-area: lsidebar;
  padding: 10px;
  height: 528px;
  /* min-height: 528px; */
  display: block;
}

.ScrollbarsCustom-Content {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 200px);
  grid-auto-rows: 80px;
  grid-row-gap: 20px;
  /* height: 460px; */
  height: 528px;
  // overflow-y: scroll;
}
.ScrollbarsCustom-Track {
  background: $c_scrollbg !important;
}
.ScrollbarsCustom-Thumb {
  // color: $c_neongreen;
  background: $c_scrollfg !important;
}

.upgrade {
  // border: 1px solid black;
  border-radius: 5px;
  display: grid;
  grid-template-rows: 40% 30% 30%;
  /* transform: rotate(5deg); */
}
.upgrade.money {
  border: 1px solid $c_moneyhover;
  // box-shadow: 0 0 25px 1px $c_moneyhover;
}
.upgrade.knowledge {
  border: 1px solid $c_knowledgehover;
  // box-shadow: 0 0 25px 1px $c_knowledgehover;
}
.upgrade.feature {
  border: 1px solid $c_featurehover;
  box-shadow: 0 0 25px 1px $c_featurehover;
}
.upgrade-buy {
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  background: transparent;
  color: $c_mainfg;
  height: 100%;
  border-radius: 5px 5px 0 0;
  padding: 0px;
  border: 0px;
  margin: 0px;
  outline: none;
  width: 100%;
  display: inline-block;
  align-items: center;
  justify-content: center;
  grid-column: 1/1;
  grid-row: 1/1;
  z-index: 2;
  // text-shadow: 0 -1px 0 #fff, 0 1px 0 #2e2e2e, 0 2px 0 #2c2c2c, 0 3px 0 #2a2a2a,
  //   0 22px 30px rgba(0, 0, 0, 0.9), 2px 2px 2px rgba(206, 89, 55, 0);
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
}
.upgrade-buy-progress {
  background: $c_darkgray;
  height: 100%;
  line-height: 40%;
  border-radius: 5px 5px 0 0;
  /* border: 2px solid red; */
  grid-column: 1/1;
  grid-row: 1/1;
}
.upgrade-buy-progress.money {
  background: $c_moneybg;
}
.upgrade-buy-progress.knowledge {
  background: $c_knowledgebg;
}
.upgrade-buy-progress.feature {
  background: $c_featurebg;
}
.upgrade-buy.canAfford.money:hover {
  // background-color: $c_moneyhover;
  background: repeating-linear-gradient(
    135deg,
    $c_moneybg,
    $c_moneybg 20px,
    $c_moneyhover 20px,
    $c_moneyhover 40px
  );
}
.upgrade-buy.canAfford.knowledge:hover {
  // background-color: $c_knowledgehover;
  background: repeating-linear-gradient(
    135deg,
    $c_knowledgebg,
    $c_knowledgebg 20px,
    $c_knowledgehover 20px,
    $c_knowledgehover 40px
  );
}
.upgrade-buy.canAfford.feature:hover {
  // background-color: $c_knowledgehover;
  background: repeating-linear-gradient(
    135deg,
    $c_featurebg,
    $c_featurebg 20px,
    $c_featurehover 20px,
    $c_featurehover 40px
  );
}
.upgrade-buy.money:active:enabled {
  background-color: $c_moneybuy !important;
  // transform: translateY(2px);
  box-shadow: inset 5px 5px 10px $c_moneybg;
}
.upgrade-buy.knowledge:active:enabled {
  background-color: $c_knowledgebuy !important;
  // transform: translateY(2px);
  box-shadow: inset 5px 5px 15px $c_knowledgebg;
}
.upgrade-buy.feature:active:enabled {
  background-color: $c_featurebuy !important;
  // transform: translateY(2px);
  box-shadow: inset 5px 5px 15px $c_featurebg;
}

.upgrade-buy-progress-pct {
  height: 100%;
  color: $c_mainfg;
  text-align: center;
  border-radius: 5px 5px 0 0;
}
.upgrade-buy-progress-pct.money {
  // background: $c_moneybuy;
  background: repeating-linear-gradient(
    135deg,
    $c_moneybg,
    $c_moneybg 20px,
    $c_moneybuy 20px,
    $c_moneybuy 40px
  );
  box-shadow: 1px 0 0 0 $c_moneyhover;
  background-size: 255px 100%;
}
.upgrade-buy-progress-pct.knowledge {
  // background: $c_knowledgebuy;
  background: repeating-linear-gradient(
    135deg,
    $c_knowledgebg,
    $c_knowledgebg 20px,
    $c_knowledgebuy 20px,
    $c_knowledgebuy 40px
  );
  box-shadow: 1px 0 0 0 $c_knowledgehover;
  background-size: 238px 100%;
}
.upgrade-buy-progress-pct.feature {
  background: repeating-linear-gradient(
    135deg,
    $c_featurebg,
    $c_featurebg 20px,
    $c_featurebuy 20px,
    $c_featurebuy 40px
  );
  box-shadow: 1px 0 0 0 $c_featurehover;
  background-size: 238px 100%;
}
.upgAnim {
  // -webkit-animation: ProgressAnim 20s infinite linear forwards;
  // -moz-animation: ProgressAnim 20s infinite linear forwards;
  // -o-animation: ProgressAnim 20s infinite linear forwards;
  // animation: ProgressAnim 2s infinite linear forwards;
}

.upgrade-buy-progress-pct span {
  display: inline-block;
  width: 100%;
  left: 0;
}

.upgrade-reward {
  // background: #444;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1px;
}
.upgrade-cost-wrapper {
  width: 100%;
  height: 100%;
  border-radius: 0 0 5px 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.upgrade-cost-wrapper.money.cannotAfford {
  color: $c_maindimfg;
}
.upgrade-cost-wrapper.knowledge.cannotAfford {
  color: $c_maindimfg;
}

/* right sidebar
-----------------------------------------------------------------------------*/

#right-sidebar {
  grid-area: rsidebar;
  background-color: $c_rsidebar;
  padding: 10px;
  height: 528px;
  display: block;
  max-width: 220px;
}
.purchaseAmts {
  width: 200px;
  height: 35px;
  // max-width: 240px;
  margin: 0 0 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
// .purchase-amount.amt-selected {
//   color: $c_mainfg;
//   background: #404;
// }

.buy-wrapper {
  width: 90%;
  display: grid;
  grid-template-columns: 40% 60%;
  grid-template-rows: 45px;
  align-items: center;
}

.buyx-prefix {
  font-size: 1.3em;
  font-weight: bold;
  margin: auto;
  // padding: 0 10px 0 0;
  color: $c_selected;
  width: 100%;
  text-align: left;
  justify-self: start;
}
.probe-prod-span {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  padding: 0 10px 0 0;
  color: $c_selected;
  width: 100%;
  text-align: left;
}
#previousProbe {
  // font-size: 1em;
  font-weight: bold;
  margin: auto;
  padding: 0 10px 0 0;
  // color: $c_selected;
  width: 100%;
  text-align: center;
}
.debugButtons {
  width: 200px;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.sidebarButtons {
  display: block;
  width: 65%;
  background: $c_mainbg;
  color: $c_mainfg;
  text-decoration: none;
  border-radius: 5px;
  border: 1px solid $c_selected;
  position: relative;
  margin: 4px;

  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
}



.fancyButtons {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/$c_selected+15,000000+30,000000+70,b51629+85 */
  background: $c_selected; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    $c_selected 03%,
    #000000 30%,
    #000000 70%,
    $c_selected 97%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    $c_selected 03%,
    #000000 30%,
    #000000 70%,
    $c_selected 97%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    $c_selected 03%,
    #000000 30%,
    #000000 70%,
    $c_selected 97%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( 
    startColorstr='$c_selected', 
    endColorstr='$c_selected',
    GradientType=1 ); /* IE6-9 */
}

.fancyButtons:hover {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/$c_selected+15,000000+30,000000+70,b51629+85 */
  background: $c_featurebuy; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    $c_featurebuy 10%,
    #000000 30%,
    #000000 70%,
    $c_featurebuy 85%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    $c_featurebuy 15%,
    #000000 30%,
    #000000 70%,
    $c_featurebuy 85%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    $c_featurebuy 15%,
    #000000 30%,
    #000000 70%,
    $c_featurebuy 85%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( 
    startColorstr='$c_featurebuy', 
    endColorstr='$c_featurebuy',
    GradientType=1 ); /* IE6-9 */
}

.fancyButtons.lit,
.fancyButtons:active {
  background: $c_selected;
  box-shadow: 0 0 25px 1px $c_selected;
}

/* Business content
-----------------------------------------------------------------------------*/

#content {
  background-color: $c_mainbg;
  grid-area: content;
  position: relative;
  width: 539px;
  /* min-height: 400px; */
  height: 528px;
  padding: 10px;
  margin: 0;
}

.container {
  margin: 5px 7px;
}

.business-container {
  width: 100%;
  margin: 5px 0;
  display: grid;
  grid-template-columns: 240px 240px;
  grid-column-gap: 20px;
  grid-auto-rows: 80px;
  grid-row-gap: 20px;
}

.business {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 45px 35px;
  border-radius: 5px;
}
.business.money {
  border: 1px solid $c_moneyhover;
}
.business.knowledge {
  border: 1px solid $c_knowledgehover;
}
.upgrade.knowledge {
  // box-shadow: 0 4px 5px 1px $c_neonblue;
}
.business,
.upgrade.money {
  // box-shadow: 0 4px 5px 1px $c_neongreen;
}

.business-buy {
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  background: transparent;
  color: $c_mainfg;
  border-radius: 5px 0 0 0;
  padding: 0px;
  border: 0px;
  margin: 0px;
  outline: none;
  grid-column: 1/3;
  grid-row: 1;
  z-index: 2;
  // height: 42px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
  // text-shadow: 0px 0px 5px rgba(0, 0, 0, 1);
}
.business-buy-progress.money {
  background: $c_moneybg;
}
.business-buy-progress.knowledge {
  background: $c_knowledgebg;
}
.business-buy.canAfford.money:hover {
  background-color: $c_moneyhover;
  // color: #000;
}
.business-buy.canAfford.knowledge:hover {
  background-color: $c_knowledgehover;
}
.business-buy:active:enabled {
  background-color: $c_moneybuy !important;
  box-shadow: inset 3px 3px 10px $c_moneybg;
}
.business-buy-progress {
  border-radius: 5px 0 0 0;
  /* border: 2px solid red; */
  grid-column: 1/3;
  grid-row: 1;
  // height: 42px;
  border-right: 1px solid $c_moneydivider;
}

.business-buy-progress-pct {
  height: 100%;
  color: $c_mainfg;
  text-align: center;
  border-radius: 5px 0 0 0;
}
.business-buy-progress-pct.money {
  background: $c_moneybuy;
}
.business-buy-progress-pct.knowledge {
  background: $c_knowledgebuy;
}

.business-buy-progress-pct span {
  display: inline-block;
  width: 100%;
  left: 0;
}
.business-buy.canAfford,
.upgrade-buy.canAfford,
.purchase-amount {
  cursor: pointer;
}
.business-buy.cannotAfford,
.upgrade-buy.cannotAfford {
  cursor: not-allowed;
}

.business-earn-progress {
  border-radius: 0;
  /* border: 2px solid red; */
  grid-column: 1/3;
  grid-row: 2;
  position: relative;
  height: 3px;
  background: $c_countdownbg;
  border-right: 1px solid $c_moneydivider;
}
.business-earn-progress-pct {
  height: 3px;
  position: absolute;
  top: 0;
  left: 0;
}
.business-earn-progress-pct span {
  height: 3px;
  // display: inline-block;
  display: block;
  width: 100%;
  background: $c_countdownfg;
  border-radius: 2px;
}

.buyMultiple {
  color: $c_mainfg;
  text-shadow: -1px -1px 0 $c_selected, 1px -1px 0 $c_selected,
    -1px 1px 0 $c_selected, 1px 1px 0 $c_selected;
}

.overlays-wrapper {
  grid-column: 1/3;
  grid-row: 1;
  height: 100%;
  z-index: 3;
  // outline: 1px solid blue;
  pointer-events: none;
  // text-align:center;
}
.overlay-text {
  font-size: 2em;
  font-weight: bold;
  // color: red;
  // outline: 1px solid red;
  position: absolute;
  pointer-events: none;
  // display: inline-block;
}

.business-owned {
  font-size: 1.3em;
  font-weight: bold;
  text-decoration: none;
  height: 100%;
  border-radius: 0 5px 0 0;
  padding: 0px;
  border: 0px;
  margin: 0px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-column: 3;
  grid-row: 1;
}
.business-owned {
  background: $c_mainbg;
  color: $c_mainfg;
}

.cost-wrapper {
  // padding: 0 5px 0 5px;
  padding: 0 1px;
  border-radius: 0 0 0 5px;
  grid-column: 1;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left;
  // background: #020;
  overflow: hidden;
}
.cost-wrapper.canAfford {
  color: $c_mainfg;
}
.cost-wrapper.cannotAfford {
