  justify-content: left;
  overflow: hidden;
}

.earning-pct-wrapper {
  // background: #444;
  border-radius: 0 0 5px 0;
  grid-column: 3;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Space content
-----------------------------------------------------------------------------*/

#probecontent {
