/* bonuses 
-----------------------------------------------------------------------------*/

#announcements {
  position: relative;
  background: transparent;
  // border: 1px dashed red;
  width: 559px;
  height: 545px;
  top: -585px;
  left: 245px;
  z-index: 4;
  pointer-events: none;
  overflow: hidden;
}

.announce-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  overflow: hidden;
}

.announcement {
  border: 1px solid yellow;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
  // background: $c_announcebg;
  background: repeating-linear-gradient(
    135deg,
    rgba(120, 120, 70, 0.6),
    rgba(120, 120, 70, 0.6) 20px,
    rgba(100, 109, 48, 0.6) 20px,
    rgba(100, 109, 48, 0.6) 40px
  );
  pointer-events: all;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.announce-text {
  width: 100%;
  padding: 5px 15px;
  vertical-align: middle;
  font-size: 1.2em;
  font-weight: bold;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;

  // &::before {
  //   content: "👍  ";
  // }
}

.announce-fadeout {
  visibility: hidden;
  transform: scale(0);
  opacity: 0;
  transition: all 0.4s linear;
}

