/* Stars 
------------------------------------- */

.stars {
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.stars {
  background-image: radial-gradient(
      2px 2px at 20px 30px,
      #eee,
      rgba(0, 0, 0, 0)
    ),
    radial-gradient(2px 2px at 40px 70px, #ccc, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 50px 160px, #ddd, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 90px 40px, #ccc, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 130px 80px, #ccc, rgba(0, 0, 0, 0)),
    radial-gradient(2px 2px at 160px 120px, #ddd, rgba(0, 0, 0, 0));
  background-repeat: repeat;
  background-size: 200px 200px;
  animation: starzoom 250ms infinite;
  opacity: 0;
}
.starpause {
  animation-play-state: paused;
}
.starzoom {
  animation-play-state: running;
  animation-direction: reverse;
}
.starshrink {
  animation-play-state: running;
}

.stars:nth-child(1) {
  background-position: 50% 50%;
  animation-delay: -125ms;
}
.stars:nth-child(2) {
  background-position: 20% 60%;
  animation-delay: -62ms;
}
.stars:nth-child(3) {
  background-position: -20% -30%;
  animation-delay: 0s;
}
.stars:nth-child(4) {
  background-position: 40% -80%;
  animation-delay: 62ms;
}
.stars:nth-child(5) {
  background-position: -20% 30%;
  animation-delay: 125ms;
}

@keyframes starzoom {
  0% {
    opacity: 0;
    transform: scale(0.5);
    animation-timing-function: ease-in;
  }
  85% {
    opacity: 1;
    transform: scale(1.8);
    animation-timing-function: linear;
  }
  100% {
    opacity: 0;
    transform: scale(2.5);
  }
}
