.onoffswitch {
  position: relative;
  width: 60px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none; }

.onoffswitch-checkbox {
  display: none; }

.onoffswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 2px solid #999999;
  border-radius: 25px; }

.onoffswitch-inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  -webkit-transition: margin 0.3s ease-in 0s;
  transition: margin 0.3s ease-in 0s; }

.onoffswitch-inner:before, .onoffswitch-inner:after {
  display: block;
  float: left;
  width: 50%;
  height: 14px;
  padding: 0;
  line-height: 14px;
  font-size: 14px;
  color: white;
  font-family: Trebuchet, Arial, sans-serif;
  font-weight: bold;
  box-sizing: border-box; }

.onoffswitch-inner:before {
  content: "ON";
  padding-left: 10px;
  background-color: #1a8a09;
  color: #FFFFFF; }

.onoffswitch-inner:after {
  content: "OFF";
  padding-right: 10px;
  background-color: #b51629;
  color: #FFFFFF;
  text-align: right; }

.onoffswitch-switch {
  display: block;
  width: 12px;
  margin: 1px;
  background: #FFFFFF;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 42px;
  border: 2px solid #999999;
  border-radius: 25px;
  -webkit-transition: all 0.3s ease-in 0s;
  transition: all 0.3s ease-in 0s; }

.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
  margin-left: 0; }

.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
  right: 0px; }

#concentrate-container {
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

#concentrateButton {
  position: relative;
  display: block;
  width: 65%;
  height: 60px;
  margin: 10px auto 10px auto;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: #ddd;
  text-decoration: none;
  border-radius: 35px;
  outline: none;
  cursor: pointer;
  background: -webkit-gradient(linear, left top, left bottom, from(#49fb35), to(#000));
  background: linear-gradient(#49fb35, #000);
  border: 1px solid #579a00;
  box-shadow: inset 0 2px 2px #49fb35, inset 1px 0 3px #b2dc2d, inset -1px 0 3px #b2dc2d, inset 0 -2px 2px #082a02, 0 0 3px rgba(0, 0, 0, 0.24);
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000, -3px 3px 3px #333;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

#concentrateButton:hover {
  background: -webkit-gradient(linear, left top, left bottom, from(#49fb35), to(#082a02));
  background: linear-gradient(#49fb35, #082a02); }

#concentrateButton.concActive,
#concentrateButton:active {
  font-size: 24px;
  cursor: not-allowed;
  background: none;
  box-shadow: inset 3px 3px 10px #082a02;
  color: #49fb35;
  -webkit-animation: concPulse 2s ease infinite, concShake 1s linear infinite;
          animation: concPulse 2s ease infinite, concShake 1s linear infinite;
  -webkit-transition: all 1s ease;
  transition: all 1s ease; }

#concentrateButton.concCooldown {
  cursor: not-allowed;
  background: none;
  background-color: #1a8a09;
  box-shadow: inset 3px 3px 10px #082a02;
  color: #888;
  -webkit-transition: all 1s ease;
  transition: all 1s ease; }

@-webkit-keyframes concPulse {
  0%,
  30%,
  70%,
  100% {
    background-color: #49fb35; }
  50% {
    background-color: #1a8a09; } }

@keyframes concPulse {
  0%,
  30%,
  70%,
  100% {
    background-color: #49fb35; }
  50% {
    background-color: #1a8a09; } }

@-webkit-keyframes concShake {
  10%,
  90% {
    -webkit-transform: translate3d(1px, 1px, 0);
            transform: translate3d(1px, 1px, 0); }
  20%,
  80% {
    -webkit-transform: translate3d(-1px, -1px, 0);
            transform: translate3d(-1px, -1px, 0); }
  30%,
  50%,
  70% {
    -webkit-transform: translate3d(1px, -1px, 0);
            transform: translate3d(1px, -1px, 0); }
  40%,
  60% {
    -webkit-transform: translate3d(-1px, 1px, 0);
            transform: translate3d(-1px, 1px, 0); } }

@keyframes concShake {
  10%,
  90% {
    -webkit-transform: translate3d(1px, 1px, 0);
            transform: translate3d(1px, 1px, 0); }
  20%,
  80% {
    -webkit-transform: translate3d(-1px, -1px, 0);
            transform: translate3d(-1px, -1px, 0); }
  30%,
  50%,
  70% {
    -webkit-transform: translate3d(1px, -1px, 0);
            transform: translate3d(1px, -1px, 0); }
  40%,
  60% {
    -webkit-transform: translate3d(-1px, 1px, 0);
            transform: translate3d(-1px, 1px, 0); } }

/* Layout
-----------------------------------------------------------------------------*/
body {
  margin: 8px;
  color: #ddd;
  background: #000;
  display: flex;
  justify-content: center;
  /* don't select text */
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome and Opera */ }

button {
  font-weight: bold; }

#wrapper {
  width: 1024px;
  max-width: 1024px;
  height: 700px;
  display: grid;
  grid-template-columns: 1024px;
  grid-template-areas: "header " "tabs-container" "footer "; }

.react-tabs-container {
  grid-area: tabs-container;
  width: 1024px;
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-template-areas: "tabs" "panel-main"; }

.main-tab-panel {
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 245px 559px 220px;
  grid-template-areas: "lsidebar content rsidebar"; }

.probe-tab-panel {
  background: #000;
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 804px 220px;
  grid-template-areas: "content rsidebar"; }

.prestige-tab-panel {
  background: #000;
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 804px 220px;
  grid-template-areas: "content rsidebar"; }

.settings-tab-panel {
  background: #000;
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 804px 220px;
  grid-template-areas: "content rsidebar"; }

/* Misc
-----------------------------------------------------------------------------*/
:focus {
  outline: none; }

::-moz-focus-inner {
  border: 0; }

.moneySymbol {
  color: #29b516; }

.knowledgeSymbol {
  color: #b55b16; }

.prestigeSymbol {
  color: #b51629; }

.concentrateSymbol {
  font-size: 20px;
  color: white; }

/* Header
-----------------------------------------------------------------------------*/
#header {
  height: 60px;
  grid-area: header;
  background-color: #000;
  padding: 5px; }

.header-wrapper {
  height: 60px;
  display: grid;
  grid-template-columns: 39% 22% 39%; }

.income {
  font-size: 1.2em;
  /* width: 300px; */
  display: grid;
  grid-template-columns: 30% 30% 40%;
  grid-template-rows: 30px 30px; }

.income-header {
  grid-column: 1;
  grid-row: 1;
  padding: 0 10px;
  margin: auto 0 auto auto; }

.income-money {
  grid-column: 2;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.income-earning {
  grid-column: 3;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.learning-header {
  grid-column: 1;
  grid-row: 2;
  padding: 0 10px;
  margin: auto 0 auto auto; }

.learning-knowledge {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

.learning-learning {
  grid-column: 3;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

.title-wrapper {
  height: 60px; }

.prestige {
  font-size: 1.2em;
  display: grid;
  grid-template-columns: 110px 110px 150px;
  grid-template-rows: 30px 30px; }

.prestige-header {
  grid-column: 1;
  padding: 0 10px;
  margin: auto 0 auto auto; }

.probe-header {
  grid-column: 1;
  grid-row: 2;
  padding: 0 10px;
  margin: auto 0 auto auto; }

.probe-distance {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

.probe-rate {
  grid-column: 3;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

.prestige-units {
  grid-column: 2;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.prestige-earning {
  grid-column: 3;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.prestige-boost {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

/* tabs
-----------------------------------------------------------------------------*/
#tabs {
  height: 30px;
  grid-area: tabs;
  background-color: #000;
  padding: 0; }

.tab-list {
  height: 26px;
  margin: 0;
  padding: 0 15px;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid #4d4dff; }

.tab-list-item {
  display: inline-block;
  width: 100px;
  text-align: center;
  margin: 0 15px;
  font-weight: bold;
  background: #0f0f56;
  border: 1px solid #4d4dff;
  border-bottom: none;
  bottom: -1px;
  padding: 5px;
  cursor: pointer;
  color: #888; }

.tab-list-item.spacer-tab {
  flex-grow: 1;
  opacity: 0;
  pointer-events: none; }

.react-tabs__tab--selected {
  background: #4d4dff;
  color: #ddd; }

.react-tabs__tab-panel {
  display: none; }

.react-tabs__tab-panel--selected {
  display: grid; }

/* footer
-----------------------------------------------------------------------------*/
#footer {
  height: 41px;
  grid-area: footer;
  background-color: #000;
  padding: 5px;
  border-top: 1px solid #4d4dff;
  opacity: 0.6;
  -webkit-transition: opacity 1s ease 0.3s;
  transition: opacity 1s ease 0.3s;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-auto-rows: 50% 50%; }

#footer:hover {
  opacity: 1;
  -webkit-transition: opacity 0.5s ease 0.1s;
  transition: opacity 0.5s ease 0.1s; }

#fps {
  grid-column: 1;
  grid-row: 2; }

#savegame {
  font-weight: bold;
  opacity: 1; }

#savegame.game-saved {
  text-align: center;
  margin: auto;
  grid-column: 8;
  grid-row: 1;
  -webkit-transition: opacity 5s ease-in;
  transition: opacity 5s ease-in;
  opacity: 0; }

/* left sidebar
-----------------------------------------------------------------------------*/
.left-sidebar {
  background-color: #000;
  grid-area: lsidebar;
  padding: 10px;
  height: 528px;
  /* min-height: 528px; */
  display: block; }

.ScrollbarsCustom-Content {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 200px);
  grid-auto-rows: 80px;
  grid-row-gap: 20px;
  /* height: 460px; */
  height: 528px; }

.ScrollbarsCustom-Track {
  background: #082a02 !important; }

.ScrollbarsCustom-Thumb {
  background: #1a8a09 !important; }

.upgrade {
  border-radius: 5px;
  display: grid;
  grid-template-rows: 40% 30% 30%;
  /* transform: rotate(5deg); */ }

.upgrade.money {
  border: 1px solid #29b516; }

.upgrade.knowledge {
  border: 1px solid #4d4dff; }

.upgrade.feature {
  border: 1px solid #EE102A;
  box-shadow: 0 0 25px 1px #EE102A; }

.upgrade-buy {
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  background: transparent;
  color: #ddd;
  height: 100%;
  border-radius: 5px 5px 0 0;
  padding: 0px;
  border: 0px;
  margin: 0px;
  outline: none;
  width: 100%;
  display: inline-block;
  align-items: center;
  justify-content: center;
  grid-column: 1/1;
  grid-row: 1/1;
  z-index: 2;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.upgrade-buy-progress {
  background: #333;
  height: 100%;
  line-height: 40%;
  border-radius: 5px 5px 0 0;
  /* border: 2px solid red; */
  grid-column: 1/1;
  grid-row: 1/1; }

.upgrade-buy-progress.money {
  background: #082a02; }

.upgrade-buy-progress.knowledge {
  background: #030d2f; }

.upgrade-buy-progress.feature {
  background: #3d070d; }

.upgrade-buy.canAfford.money:hover {
  background: repeating-linear-gradient(135deg, #082a02, #082a02 20px, #29b516 20px, #29b516 40px); }

.upgrade-buy.canAfford.knowledge:hover {
  background: repeating-linear-gradient(135deg, #030d2f, #030d2f 20px, #4d4dff 20px, #4d4dff 40px); }

.upgrade-buy.canAfford.feature:hover {
  background: repeating-linear-gradient(135deg, #3d070d, #3d070d 20px, #EE102A 20px, #EE102A 40px); }

.upgrade-buy.money:active:enabled {
  background-color: #1a8a09 !important;
  box-shadow: inset 5px 5px 10px #082a02; }

.upgrade-buy.knowledge:active:enabled {
  background-color: #163bb5 !important;
  box-shadow: inset 5px 5px 15px #030d2f; }

.upgrade-buy.feature:active:enabled {
  background-color: #b51629 !important;
  box-shadow: inset 5px 5px 15px #3d070d; }

.upgrade-buy-progress-pct {
  height: 100%;
  color: #ddd;
  text-align: center;
  border-radius: 5px 5px 0 0; }

.upgrade-buy-progress-pct.money {
  background: repeating-linear-gradient(135deg, #082a02, #082a02 20px, #1a8a09 20px, #1a8a09 40px);
  box-shadow: 1px 0 0 0 #29b516;
  background-size: 255px 100%; }

.upgrade-buy-progress-pct.knowledge {
  background: repeating-linear-gradient(135deg, #030d2f, #030d2f 20px, #163bb5 20px, #163bb5 40px);
  box-shadow: 1px 0 0 0 #4d4dff;
  background-size: 238px 100%; }

.upgrade-buy-progress-pct.feature {
  background: repeating-linear-gradient(135deg, #3d070d, #3d070d 20px, #b51629 20px, #b51629 40px);
  box-shadow: 1px 0 0 0 #EE102A;
  background-size: 238px 100%; }

.upgrade-buy-progress-pct span {
  display: inline-block;
  width: 100%;
  left: 0; }

.upgrade-reward {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1px; }

.upgrade-cost-wrapper {
  width: 100%;
  height: 100%;
  border-radius: 0 0 5px 5px;
  display: flex;
  align-items: center;
  justify-content: center; }

.upgrade-cost-wrapper.money.cannotAfford {
  color: #888; }

.upgrade-cost-wrapper.knowledge.cannotAfford {
  color: #888; }

/* right sidebar
-----------------------------------------------------------------------------*/
#right-sidebar {
  grid-area: rsidebar;
